body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --header-text-color: #ec7f37;
  --main-bg-color: #1d1e22;
  --main-text-color: #ffffff;
  --close-modal-color: #ffffff;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.gallery {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.project {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: #ffffff;
  color: var(--main-text-color);
  padding: 5%;
}

.project-title {
  padding-bottom: 2%;
}

.header > div {
  padding: 10px;
  text-align: center;
  font-size: 2em;
  color: #ec7f37;
  color: var(--header-text-color);
}

html,
body {
  background-color: #1d1e22;
  background-color: var(--main-bg-color);
  margin: 10px;
}

.profileText {
  color: #ffffff;
  color: var(--main-text-color);
}

.project-modal {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: #ffffff;
  color: var(--main-text-color);
}

.project-modal h3 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.modal-close {
  color: #ffffff;
  color: var(--close-modal-color);
}

.modal-icons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding-bottom: 2%;
}

.modal-desc {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-left: 5%;
  padding-right: 5%;
}

.modal-dl {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.modal-dl img {
  padding-left: 2%;
  padding-right: 2%;
}

.closeIconColor {
  color: #ffffff;
  color: var(--close-modal-color);
}

